import React, { useState } from "react";
import mindfullogo from "../../assets/images/FinishLogo.svg";
import { BsArrowRight } from "react-icons/bs";
import FormDetails from './form';
import validate from './validation';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {
  Wrapper,
  Container,
  LogoWrapper,
  AppHead,
  AppP,
  FormWrapper,
  FormContentWrap,
  FormContentButton,
  FormContentLoader,
  FormError,
} from "./style";

import Loader from "react-loader-spinner";
const Appointment = ({ submitForm }) => {
  const {
    handleChange,
    values,
    handleSubmit,
    errors,
    submitError,
    isSubmitting,
  } = FormDetails(submitForm, validate);

  return (
    <Wrapper>
      <Container>
        <LogoWrapper>
          <img src={mindfullogo} alt="mindfuliving" />
        </LogoWrapper>
        <AppHead>Make an Appointment</AppHead>
        <AppP>Please enter your name and contact information.</AppP>
        {submitError.submiterror && (
          <FormError>{submitError.submiterror}</FormError>
        )}
        <FormWrapper onSubmit={handleSubmit}>
          <FormContentWrap>
            <label>Firstname</label>
            <input
              type="text"
              name="firstname"
              placeholder="firstname"
              value={values.firstname}
              onChange={handleChange}
            />
            {errors.firstname && <FormError>{errors.firstname}</FormError>}
          </FormContentWrap>
          <FormContentWrap>
            <label>Lastname</label>
            <input
              type="text"
              name="lastname"
              placeholder="lastname"
              value={values.lastname}
              onChange={handleChange}
            />
            {errors.lastname && <FormError>{errors.lastname}</FormError>}
          </FormContentWrap>
          <FormContentWrap>
            <label>Email</label>
            <input
              type="email"
              name="email"
              value={values.email}
              onChange={handleChange}
            />
            {errors.email && <FormError>{errors.email}</FormError>}
          </FormContentWrap>
          <FormContentWrap>
            <label>Phone</label>
            <input
              type="text"
              name="phoneNumber"
              value={values.phoneNumber}
              onChange={handleChange}
            />
            {errors.phoneNumber && <FormError>{errors.phoneNumber}</FormError>}
          </FormContentWrap>
          {!isSubmitting ? (
            <FormContentButton type="submit">
              <p>Continue</p>
              <div>
                <BsArrowRight />
              </div>
            </FormContentButton>
          ) : (
            <FormContentLoader>
              <Loader type="TailSpin" color="#ffffff" height={30} width={80} />
            </FormContentLoader>
          )}
        </FormWrapper>
      </Container>
    </Wrapper>
  );
};

export default Appointment;
