import React from 'react'
import { Wrapper, CircleWrap, Circle1, Circle2 } from "./styles";
const CircleButtons = ({success}) => {
    return (
      <Wrapper>
        <CircleWrap>
          <Circle1 success={success} />
          <Circle2 success={success} />
        </CircleWrap>
      </Wrapper>
    );
}

export {CircleButtons}
