import styled from 'styled-components';
import { Link as ScrollLink } from "react-scroll";
export const Wrapper = styled.div`
  height: 100vh;
  width: 100%;
  position: fixed;
  top: ${({ mobileactive }) => (mobileactive ? "0" : "-100%")};
  opacity: ${({ mobileactive }) => (mobileactive ? "100%" : "0")};
  background: white;
  z-index:20;
`;

export const CloseIcon = styled.div`
position:absolute;
top:40px;
right: 30px;
font-size:30px;
`;
export const Container = styled.div`
display:flex;
justify-content:center;
flex-direction:column;
align-items:center;
width:100%;
height:100%;
`;

export const MenuWrapper = styled.div`
display:flex;
flex-direction:column;
align-items:center;

`;

export const MenuWrap = styled.div`
display:flex;
flex-direction:column;
align-items:center;
`;

export const MenuItem = styled(ScrollLink)`
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #348e91;
  margin-bottom: 40px;
`;

export const SocialsWrap = styled.div`
  display: flex;
  align-items: center;
  font-size: 20px;
  justify-content:space-between;
  margin-top: 30px;
  color: #c9c9c9;
  width:200px;
  hr {
    width: 20px;
    transform: rotate(270deg);
  }
`;