import React from "react";
import logoImageS from '../../../assets/images/mindfuliving.svg';
import {
  Wrapper,
  Container,
  MenuContactWrap,
  MenuWrapper,
  LogoWrap,
  MenuWrap,
  MenuItem,
  AppWrap,
  SocialsWrap,
  HamSection,
  ContactE,
} from "./style";

import { FaFacebookF, FaTwitter, FaLinkedinIn } from "react-icons/fa";
import { AiFillInstagram } from "react-icons/ai";
import { BiMenuAltRight } from "react-icons/bi";
const Navigation = ({toggle}) => {
  return (
    <Wrapper>
      <Container>
        <MenuContactWrap>
          <ContactE >
          <p>0772310828</p> <p>|</p> <p>mindfulivingug@gmail.com</p></ContactE>

          <AppWrap to="/appointment">
            <h5>Make an appointment</h5>
          </AppWrap>
        </MenuContactWrap>

        <MenuWrapper>
          <LogoWrap>
            <img src={logoImageS} alt="mindfuliving" />
          </LogoWrap>
          <MenuWrap>
            <MenuItem
              to="home"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Home
            </MenuItem>
            <MenuItem
              to="about"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              About
            </MenuItem>
            <MenuItem
              to="services"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Services
            </MenuItem>
            <MenuItem
              to="contact"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Contact
            </MenuItem>
          </MenuWrap>
          <HamSection onClick={toggle}>
            <BiMenuAltRight />
          </HamSection>
          <SocialsWrap>
            <FaFacebookF />
            <div>|</div>
            <FaTwitter />
            <div>|</div>
            <AiFillInstagram />
            <div>|</div>
            <FaLinkedinIn />
          </SocialsWrap>
        </MenuWrapper>
      </Container>
    </Wrapper>
  );
};

export { Navigation };
