import React from "react";
import {
  Wrapper,
  Container,
  MenuWrapper,
  MenuWrap,
  MenuItem,
  SocialsWrap,
  CloseIcon,
} from "./style";

import { FaFacebookF, FaTwitter, FaLinkedinIn } from "react-icons/fa";
import { AiFillInstagram } from "react-icons/ai";
import { GrClose } from "react-icons/gr";
const MobileMenu = ({ mobileActive, toggle }) => {
  return (
    <Wrapper mobileactive={mobileActive}>
      <Container>
        <CloseIcon onClick={toggle}>
          <GrClose />
        </CloseIcon>
        <MenuWrapper>
          <MenuWrap>
            <MenuItem
              to="home"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Home
            </MenuItem>
            <MenuItem
              to="about"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              About
            </MenuItem>
            <MenuItem
              to="services"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Services
            </MenuItem>
            <MenuItem
              to="contact"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              Contact
            </MenuItem>
          </MenuWrap>

          <SocialsWrap>
            <FaFacebookF />
            <div>
              <hr />
            </div>
            <FaTwitter />
            <div>
              <hr />
            </div>
            <AiFillInstagram />
            <div>
              <hr />
            </div>
            <FaLinkedinIn />
          </SocialsWrap>
        </MenuWrapper>
      </Container>
    </Wrapper>
  );
};

export { MobileMenu };
