import styled from 'styled-components';

export const Wrapper = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  @media all and (min-width: 1366px) {
    width: 1000px;
    height: 100%;
  }
`;

export const LogoWrapper = styled.div`
  height: 124.92px;
  margin-bottom: 35px;
  margin-top: 15px;
`;

export const AppHead = styled.div`
  width: 292.14px;
  font-size: 23.1202px;
  line-height: 27px;
  margin-bottom: 5px;
  

  @media all and (min-width: 1366px) {
    font-size: 23.1202px;
    line-height: 27px;
    color: #5f5f5f;
    text-align: left;
    width: 371.05px;
    margin-bottom: 7px;
  }
`;

export const AppP = styled.div`
  width: 292.14px;
  font-size: 13.5484px;
  line-height: 16px;
  color: #818286;
  margin-bottom: 18px;

  @media all and (min-width: 1366px) {
    width: 371.05px;
    font-weight: normal;
    font-size: 13.5484px;
    line-height: 16px;
    color: #818286;
    margin-bottom: 28px;
  }
`;

export const FormWrapper = styled.form`
 
`;

export const FormContentWrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 18px;
  label {
    font-size: 15px;
    line-height: 14px;
    margin-bottom: 10px;
    color: #000000;
  }
  input {
    background: rgba(246, 233, 235, 0.3);
    border: 1.29134px solid #cae3e3;
    border-radius: 7.05794px;
    width: 292.14px;
    height: 33.34px;
    outline: none;
    text-indent: 10px;
  }

  @media all and (min-width: 1366px) {
    width: 371.05px;
    display: flex;
    flex-direction: column;
    margin-bottom: 28px;

    label {
      font-size: 18px;
      line-height: 16px;
      margin-bottom: 11px;
      color: #000000;
    }
    input {
      background: rgba(246, 233, 235, 0.3);
      border: 1.29134px solid #cae3e3;
      border-radius: 7.05794px;
      width: 371.05px;
      height: 41.34px;
      outline: none;
    }
  }
`;

export const FormError = styled.p`
  color: red;
  font-size:14px;
  margin-bottom:0;
  @media all and (min-width:1366px){
    font-size: 15px;
  }
`;

export const FormContentButton = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  background: #348e91;
  color: #ffffff;
  border-radius: 7.05794px;
  outline: none;
  border: 1px solid #348e91;
  width: 292.14px;
  height: 41.34px;
  p {
    margin-right: 5px;
    font-size: 15px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  div {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    padding-top: 4px;
    height: 100%;
  }
  @media all and (min-width: 1366px) {
    width: 371.05px;
    height: 41.34px;
    p {
      margin-right: 5px;
      font-size: 15px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    div {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      padding-top: 4px;
      height: 100%;
    }
  }
`;

export const FormContentLoader = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background: #348e91;
  border-radius: 7.05794px;
  outline: none;
  border: 1px solid #348e91;
  width: 292.14px;
  height: 41.34px;

  @media all and (min-width: 1366px) {
    width: 371.05px;
    height: 41.34px;
  }
`;

