import React from 'react'
import {
  Wrapper,
  LogoWrapper,
  FinishContainer,
  ContentMessage,
  ConfirmMessage,
  BackButton,
} from "./successStyle";
import mindfullogo from "../../assets/images/FinishLogo.svg";
import thankyou from "../../assets/images/Tick.svg";

const Success = ({email}) => {
  
    return (
      <Wrapper>
        <FinishContainer>
          <LogoWrapper>
            <img src={mindfullogo} alt="mindfuliving" />
          </LogoWrapper>
          <ContentMessage>
            <img src={thankyou} alt="tick" />
            <h5>Thanks! We'll message you</h5>
          </ContentMessage>

          <ConfirmMessage>
            <h4>We've sent a confirmation to</h4>
            <h5>{email}</h5>
          </ConfirmMessage>

          <BackButton to="/">Back to website</BackButton>
        </FinishContainer>
      </Wrapper>
    );
}

export default Success
 