import {useState, useEffect} from "react";
import axios from 'axios';

const FormDetails = (Formsubmit, validate)=>{
    const [values, setValues] = useState({
        firstname:"",
        lastname:"",
        email:"",
        phoneNumber: ""
    });

    const [submitError, setSubmitError] = useState({});
    const [errors, setErrors] = useState({});
    const [isSubmitting, setIsSubmit]=useState(false);

    //values changing on input
    const handleChange=(e)=>{
        setValues({
            ...values,
            [e.target.name]: e.target.value
        });
    };

    //action of submit on the form
    const handleSubmit = (e)=>{
        e.preventDefault();
        setErrors(validate(values));
        setIsSubmit(true);
    };

    useEffect(()=>{
        if (Object.keys(errors).length === 0 && isSubmitting){
            axios
              .post("https://mindfuliving.herokuapp.com/send/", values)
              .then((success) => {
                Formsubmit(values.email);
              })
              .catch((error) => {
                setSubmitError({
                  submiterror: "message not sent",
                });
                setIsSubmit(false);
              });
        }else {
            setIsSubmit(false);
        }
    }, [errors, isSubmitting,Formsubmit, setSubmitError, values]);

    return {handleChange, values, handleSubmit, errors, submitError, isSubmitting}
};

export default FormDetails;