import { Link } from "react-router-dom";
import styled from "styled-components";

export const Wrapper = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media all and (min-width:1366px){
    margin-bottom:40px;
  }
  
`;

export const LogoWrapper = styled.div`
  height: 124.92px;
  margin-bottom: 25px;
  margin-top: 15px;
`;

export const FinishContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  @media all and (min-width: 1366px) {
    height: 100%;
  }
`;

export const ContentMessage = styled.div`
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 23px;
  img {
    margin-bottom: 23px;
  }
  h5 {
    font-weight: bold;
    font-size: 23.6px;
    line-height: 28px;

    color: #262626;
  }

  @media all and (min-width: 1366px) {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 43px;
    img {
      margin-bottom: 23px;
    }
    h5 {
      font-weight: bold;
      font-size: 27.2877px;
      line-height: 33px;

      color: #262626;
    }
  }
`;

export const ConfirmMessage = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80px;
  h4 {
    font-weight: normal;
    font-size: 20.4658px;
    line-height: 25px;

    color: #807e7e;
  }
  h5 {
    font-weight: 900;
    font-size: 14.7808px;
    line-height: 18px;
    margin-top: 8px;

    color: #5f5f5f;
  }

  @media all and (min-width: 1366px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 143px;
    h4 {
      font-weight: normal;
      font-size: 20.4658px;
      line-height: 25px;

      color: #807e7e;
    }
    h5 {
      font-weight: 900;
      font-size: 14.7808px;
      line-height: 18px;
      margin-top: 8px;

      color: #5f5f5f;
    }
  }
`;

export const BackButton = styled(Link)`
  display: flex;
  align-items: center;
  justify-content: center;
  background: #348e91;
  color: #ffffff;
  border-radius: 7.05794px;
  outline: none;
  border: 1px solid #348e91;
  cursor: pointer;
  text-decoration: none;
  width: 292.14px;
  height: 41.34px;

  @media all and (min-width: 1366px) {
    width: 371.05px;
    height: 41.34px;
  }
`;
