import { Link as RouterLink } from "react-router-dom";
import { Link as ScrollLink } from "react-scroll";
import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  position: fixed;
  z-index: 10;
`;

export const Container = styled.div``;

export const MenuContactWrap = styled.div`
  background: #262626;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;

  p {
    font-size: 13px;
    
  }
  @media all and (max-width: 768px) {
    display: none;
  }
`;
export const ContactE = styled.div`
margin-left:10px;
width:250px;
    display:flex;
    justify-content:space-between;
`;

export const AppWrap = styled(RouterLink)`
  text-decoration: none;
  height: 100%;
  width: 200px;
  background: grey;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
`;

export const MenuWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  background: white;
`;

export const LogoWrap = styled.div`
  img {
    width: 178px;
  }
  margin-left: 20px;
  @media all and (min-width: 1366px) {
    img {
      width: 208px;
    }
    margin-left: 20px;
  }
`;

export const MenuWrap = styled.nav`
   display: flex;
  justify-content: space-between;
  width: 350px;
  color: #348e91;
@media all and (min-width: 768px){
     display: flex;
  justify-content: space-between;
  width: 250px;
  color: #348e91;
}
@media all and (min-width:1366px){
    display: flex;
  justify-content: space-between;
  width: 350px;
  color: #348e91;
}

  @media all and (max-width: 767px) {
    display: none;
  }
`;

export const HamSection = styled.div`
  font-size: 40px;
  color: #1d5052;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  @media all and (min-width: 768px) {
    display: none;
  }
`;
export const MenuItem = styled(ScrollLink)`
  cursor: pointer;
  text-decoration: none;
  color: #348e91;
  font-size: 20px;
  line-height: 24px;

  &:hover {
    color: red;
  }
  @media all and (min-width: 768px) {
    cursor: pointer;
    text-decoration: none;
    color: #348e91;
    font-size: 16px;
    line-height: 24px;
  }
  @media all and (min-width: 1366px) {
    cursor: pointer;
    text-decoration: none;
    color: #348e91;
    font-size: 20px;
    line-height: 24px;
  }
`;
export const SocialsWrap = styled.div`
  font-size: 20px;
  color: #c9c9c9;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  @media all and (max-width: 768px) {
    display: none;
  }
  @media all and (min-width: 1366px) {
    margin-right: 30px;
  }
`;
